if (document.querySelector('.body__projects')) {
   // hero count animation
   const animationDuration = 2000;
   const frameDuration = 1000 / 60;
   const totalFrames = Math.round(animationDuration / frameDuration);
   const easeOutQuad = t => t * (2 - t);
   const animateCountUp = el => {
      let frame = 0;
      const countTo = parseInt(el.innerHTML, 10);
      const counter = setInterval(() => {
         frame++;
         const progress = easeOutQuad(frame / totalFrames);
         const currentCount = Math.round(countTo * progress);
         if (parseInt(el.innerHTML, 10) !== currentCount) {
            el.innerHTML = currentCount;
         }
         if (frame === totalFrames) {
            clearInterval(counter);
         }
      }, frameDuration);
   };
   window.addEventListener('load', () => {
      const countupEls = document.querySelectorAll('#count');
      countupEls.forEach(animateCountUp);
   })
}
if (document.querySelector('.accordion__item')) {
   // accordion
   function accordion() {
      const items = document.querySelectorAll('.accordion__header')
      items.forEach(item => {
         item.addEventListener('click', () => {
            const parent = item.parentNode
            if (parent.classList.contains('show')) {
               parent.classList.remove('show')
            } else {
               document
                  .querySelectorAll('.accordion__item')
                  .forEach(child => child.classList.remove('show'))
               parent.classList.add('show')
            }
         })
      })
   }
   accordion()
}